import React from 'react';
import { Switch, Route, HashRouter, Link } from 'react-router-dom';

//Main pages
import LandingPage from './landingpage';
import AboutMe from './aboutmepage';
import FoodPage from  './foodpage';
import Project from './projectspage';

//Other website pages
import Osucompare from './projects/osucompare.js';


//Food list pages
import _1 from './food-articles/00001.js';
import _2 from './food-articles/00002.js';
import _3 from './food-articles/00003.js';


const Main = () => (
    <HashRouter>
        <Switch>
            <Route exact path='/' component = {LandingPage}/>
            <Route exact path='/aboutme' component = {AboutMe}/>
            <Route exact path='/food' component = {FoodPage}/>
            <Route exact path='/projects' component = {Project}/>

            {/*Non-header links*/}
            <Route exact path='/osucompare' component = {Osucompare}/>

            {/*Food-list*/}
            <Route exact path='/food/1' component = {_1}/>
            <Route exact path='/food/2' component = {_2}/>
            <Route exact path='/food/3' component = {_3}/>

        </Switch>
    </HashRouter>
)

export default Main;
