import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {Grid, Cell} from 'react-mdl';

import "./foodpage.css";
import articles from './foodsidebar';
import picture from './food-articles/food-pics/00003_gummies.png';

class Food extends Component{
    render() {
        return(
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className='about-grid'>
                    <Cell col={12}>
                        <div className = 'food-text'>
                            <h1>Foods Blog</h1> 
                         
                        </div>
                    </Cell>

                    <Cell col={8}>
                        <div className = 'food-paragraph'>
                        <h1>Albanese Sour Gummies</h1>
                            <h2>Apr. 5, 2020</h2>
                            <p>With this quarantine, there’s much fewer opportunities to go out. During this time, I craved fruit candies. After leaving Popeyes a couple weeks ago, Willy and I went to Wal-Mart. I got Sweetarts Jellybeans, but returned them once I found out the self-check didn’t accept cash. That became a mistake when I constantly craved sweets for the next few weeks. A week ago, my sister and I ordered Albanese Sour Bears because I nagged her to get some with me, so I felt less guilty having an accomplice. Today, the package arrived.</p>
                            <p>When we opened the box, there was a lot less than we realized, but then we remembered that two pounds wasn’t a lot to begin with, so we both just opened the package. We arranged the gummi bears to the twelve advertised flavors, but some colors were too close to each other to immediately distinguish. My sister and I spent about fifteen minutes trying to debate if the orange/yellow colors were the same. After a while, we came up with this:</p>
                            
                            <img src={picture} height="420px" width="360px"/> 

                            
                            <p>They were all very good-looking bears, and starting from the top and going right, we tried each flavor.</p>
                            <p>Cherry - Both of us puckered at the taste: It was like cherry cough medicine, but with a huge dose of citric acid. I thought it was okay until the aftertaste. My sister didn’t like this one either.</p>
                            <p>Green apple - My sister couldn’t tell what it was until a few seconds, but I knew immediately because I don’t like green apple flavored candy. However, this one was tolerable. </p>
                            <p>Grape - We knew immediately that this would be the grape by its color, and it’s like eating grape Kool-Aid powder straight from the bag. It wasn’t bad, but it was powerful.</p>
                            <p>Blue raspberry - I love raspberry flavor, but I hate that it’s blue: my least favorite color. This was on par with other blue raspberry flavors, so it was pretty good. My sister wasn’t too happy with this flavor though.</p>
                            <p>Mango - The next one we had no idea how to distinguish it from the last pair of gummi bears appearance-wise, but I thought that these were more skin-colored than the other pair. (Now that I look at the picture, the two bears look different. The right looks more yellow.) Anyways, I got the mango flavor, and I thought it was great, but my sister insisted it was pineapple, so I can’t know for sure.</p>
                            <p>Lime - Wow, it tasted like they peeled the lime and got rid of everything inside. It was so tart and bitter, but it was definitely lime. My sister didn’t like this either.</p>
                            <p>Orange - Like other orange flavored candies, it’s always a solid flavor. These were really good.</p>
                            <p>(At this point, our mouths were getting puckered and dry from all the citric acid killing our teeth, so we got water.)</p>
                            <p>Pink grapefruit - This one was actually really tasty. My sister also liked it, comparing it to the Vitamin-C Halls candies. Real clean bitter taste unlike the lime.</p>
                            <p>Strawberry - this one was one of the best strawberry candies I’ve ever tasted. Very pronounced flavor and didn’t taste medicinal like Twizzlers' strawberry.</p>
                            <p>Watermelon - Normally, both of us dislike watermelon candies, but we agreed that this one was actually really good. No weird flavors that give me headaches.</p>
                            <p>Lemon - holy cow, this one was incredibly sour. We don’t know how else to describe it but “very sour”. Needed to drink a lot of water after that one.</p>
                            <p>Pineapple - I used to love pineapple, but after a certain event that I will not say, I do not like pineapple anymore except on pizza. This tasted like a traumatic experience, so basically the flavor was spot on.</p>
                            <p>After only twelve bears, we couldn’t eat anymore. Maybe it was because neither of us had candy in a long time, so we were more sensitive to the sugar. We felt our sugar highs and we needed to walk around to come back down from being so sugar-hopped. Out of all the flavors, I’d say my favorite was the mango with pink grapefruit, orange, and strawberry following not too far. Cherry, lime, and pineapple, which used to be my favorite flavors, fell all the way to the end of the line. Everything else in the middle were decent. I would probably get these again, but not for a while. They’re definitely high-quality gummies, that’s for sure.</p>
                        
                        </div>
                    </Cell>

                    <Cell col={4}>
                        <div className = 'sidebar'>
                            <h1>Archive</h1> 
                            {articles.map((article) => (
                                <Link to={''.concat("food/",article.id.toString())}>{article.title}</Link>))}                         
                        </div>
                    </Cell>
                </Grid>


            </div>
        )
    }
}

export default Food;
