import React, {Component} from 'react';
import {Link} from "react-router-dom";
import {Grid, Cell} from 'react-mdl';

import "../foodpage.css";
import articles from '../foodsidebar';

class Food extends Component{
    render() {
        return(
            <div style={{width: '100%', margin: 'auto'}}>
                <Grid className='about-grid'>
                    <Cell col={12}>
                        <div className = 'food-text'>
                            <h1>Foods Blog</h1> 
                         
                        </div>
                    </Cell>

                    <Cell col={8}>
                        <div className = 'food-paragraph'>
                            <h1>My First Time At Popeyes</h1>
                            <h2>Mar. 13, 2020</h2>
                            <p>Before I start my review, I have to say: Wow, what a month this was. I was going to create another review, but recently after, my neighbors had a burglary. It made my mom paranoid, so I had to stay at home for entire days to keep her from going off. A few weeks later, the COVID-19 threat has started making news, and yet again, my mom starts hoarding supplies (sorry Costco shoppers that actually needed toilet paper). After being in my house for over a month, I start getting antsy. I am an introvert, but I guess I do want to go out sometimes after all, COVID-19 pandemic or not.</p>
                            <p>Today, one of my first friends from college, Willy, called me out of the blue asked me to go out to lunch with him, and since my brother was still in the house, I agreed. Willy asked where I wanted to go, and the first thing that I thought of was Popeyes. I have never been there, and many people say the place is awesome, way better than KFC. Willy agreed with my suggestion but warned me that I’d feel like garbage after eating it, even worse than eating a Munchie Meal at Jack in the Box. When we arrived at the place, I asked Willy what’s good here. He recommended the chicken sandwich. He said the first time he had it, it was prepared like ass, but the second time, it was much better. He assured me the place we were going to would not be where he was served ass.</p>
                            <p>He ordered a 2-piece combo and I ordered the spicy chicken sandwich with a side of Cajun fries. Not even a minute later, his order was ready and mine only a few seconds after. Surprised at how fast they were, we got our food and sat down. Looking at my food, I was worried that my food would be cold since we were positive that our food was just sitting in a heater waiting for us. I opened my bag and I looked at my sandwich. The chicken was alarmingly huge. I don’t eat a lot of fast food, so I usually see McDonald’s or Wendy’s flat chicken patty. Popeye’s had a slab of meat in a bun covered in sauce. The sandwich was still warm, and I am not picky when it comes to the temperature of my food. Only if it were cold or frozen, I would say something.</p>
                            <p>The sandwich was really nice even given the fact that the chicken sitting under a heater. While the skin wasn’t crispy, it still had some texture and the meat wasn’t dry. The sauce was pleasantly spicy and kicked almost immediately. The pickles were okay. They weren’t crunchy, and it did have a bit of tang, but it was almost completely drowned out by the sauce. I remember a few months ago when the hype of the new sandwich caused many stores to close in hours, and I look down and wonder again if my taste buds were too unrefined to appreciate the taste. True, my sandwich was not fresh, but even if it were, I probably would feel the exact same about the sandwich.</p>
                            <p>I tried the fries after. They looked rather pale. (I’m sorry I didn’t take pictures of them, or of any of the food. I’m trying to gain confidence to take pictures of my food before eating.) Thankfully, they tasted way better than they looked. Seasoned, spiced fries that were somehow crispy even when lukewarm. Willy asked for their “Blackened Ranch Sauce” and gave me them, so I dipped my fries into them. They tasted even better: even more heat with the usual ranch zest that I like.</p>
                            <p>After we finished, we drove to a Wal-Mart to pick up items, and at that time, I felt slow and groggy. It was a sensation stronger than finishing a Munchie Meal. I felt my stomach gurgle and my head went foggy. I understood at that point and when I went back home, I went to sleep.</p>
                            <p>I do like Popeyes, and even with a pre-made meal, it was still pretty good. I don’t feel like coming back anytime soon, but I would not feel forced to come back immediately… or even later this month. In the meantime, I’ll still be in my house job searching. I’ll also promise to add pictures to my blog in later posts.</p>

                        </div>
                    </Cell>

                    <Cell col={4}>
                        <div className = 'sidebar'>
                            <h1>Archive</h1> 
                            {articles.map((article) => (
                                <Link to={article.id.toString()}>{article.title}</Link>))}                         
                       
                        </div>
                    </Cell>
                </Grid>


            </div>
        )
    }
}

export default Food;
